<template>
  <div class="all_wapper">
    <div v-if="!showDetail" class="makeInfo_wapper common_back">
      <div class="inner_index_content">
        <!-- 返回栏 -->
        <mt-field class="" disabled label="顶部返回栏">
          <div class="xt_flex">
            <mt-switch v-if="formInline.hasZtl" class="set_pp_p" v-model="formInline.isSystemAz">
              {{formInline.isSystemAz?'安卓':'苹果'}}</mt-switch>
            <mt-switch v-model="formInline.hasZtl">
              {{formInline.hasZtl?'有':'无'}}</mt-switch>
          </div>
        </mt-field>
        <!-- 头像 -->
        <mt-field class="tx_wapper" disabled label="微信头像" placeholder="" v-model="badInp">
          <div class="tx_div_wapper common_back hasBg" id="tx_div_wapper_mi" @click="changeHeadImg"></div>
        </mt-field>
        <!-- 昵称 -->
        <mt-field v-if="!ifPj" label="微信昵称" placeholder="必填" v-model="formInline.nickName"></mt-field>
        <!-- 朋友圈形式(内容、链接) -->
        <mt-field v-if="!ifPj" disabled label="朋友圈形式(内容、链接)" placeholder="" v-model="badInp">
          <!-- 内容 -->
          <div class="inner_no_padding">
            <mt-radio title="" v-model="formInline.contentStyle" :options="['内容','内容+链接']">
            </mt-radio>
          </div>
        </mt-field>
        <!-- 两种情况下的示例 -->
        <div class="for_example_inner">
          <span>示例：</span>
          <!-- 非拼接的 -->
          <template v-if="!ifPj">
            <!-- 内容 -->
            <template v-if="formInline.contentStyle==='内容'">
              <div class="inner_show_example common_back"></div>
            </template>
            <!-- 内容加链接 -->
            <div v-else class="inner_show_example_other_all common_back"></div>
          </template>
          <!-- 拼接的示例图 -->
          <template v-else>
            <div class="pl_show_example common_back"></div>
          </template>
        </div>
        <!-- 两种情况下的示例 end-->
        <!-- 内容情况下 -->
        <template v-if="formInline.contentStyle==='内容'">
          <!-- 内容 -->
          <mt-field v-if="!ifPj" type="textarea" label="朋友圈内容" placeholder="" v-model="formInline.realContent" placeholder="朋友圈文字内容" rows="4"></mt-field>
          <!-- 上传图片 -->
          <mt-field key="pyqimg" disabled :label="ifPj?'您的朋友圈截图':'朋友圈图片'" placeholder="" v-model="badInp" rows="4">
            <div class="xt_flex app_wapper_box">
              <!-- 遍历展示图片 -->
              <template v-for="(item,index) in formInline.realImgs">
                <div class="show_imgs common_back" :key="item+index" :style="{backgroundImage: 'url(' + item+ ')'}" @click="delImg(item,index)">
                </div>
              </template>
              <div class="nr_div_wapper common_back" id="nr_div_wapper" @click="changeContentImg"></div>
            </div>
          </mt-field>
        </template>
        <!--  内容情况下 end -->
        <!-- 内容加链接情况下 -->
        <template v-else>
          <!-- 内容 -->
          <mt-field type="textarea" label="朋友圈内容" placeholder="" v-model="formInline.realContent" placeholder="朋友圈文字内容" rows="4"></mt-field>
          <mt-field key="sltimg" disabled label="链接缩略图" v-model="badInp" class="tx_wapper">
            <div class="link_div_wapper common_back" id="link_div_wapper_p" @click="changeLinkImg"></div>
          </mt-field>
          <mt-field label="链接内容" placeholder="" v-model="formInline.smallContent" placeholder="链接里图片旁的文字">
          </mt-field>
        </template>
        <!-- 内容加链接情况下 end -->
        <!-- 时间 -->
        <mt-field label="发布时间" disabled placeholder="">
          <div class="fake_show_time" @click="getSetTime">{{formInline.sendTime}}</div>
        </mt-field>
        <!-- 时间end -->
        <mt-field class="diy_inp" label="地点" :disableClear="true" placeholder="例:北京·故宫" v-model="formInline.localtion">
          <mt-button type="primary" class="add_tbns" size="small" @click="addFgDeatail">加 ·</mt-button>
        </mt-field>
        <!-- 生成按钮 -->
        <div class="inner_make_btn">
          <mt-button type="primary" size="small" @click="postForm">保存</mt-button>
          <mt-button size="small" type="danger" @click="delForm">清空</mt-button>
          <mt-button type="primary" size="small" @click="backBtn">返回</mt-button>
          <mt-button type="primary" size="small" @click="backUrlBtn">重新选择生成方式</mt-button>
        </div>
      </div>
    </div>
    <!-- 上传组件 -->
    <common-upload ref="loadImg" key="makeInfo" :fatherClass="'makeInfo'" :ifM="setUploadM" @setImg="setImg"></common-upload>
    <!-- 评论头像选择 -->
    <mt-popup v-model="popupVisible8" class="app_pop_wapper">
      <div class="app_pop_inner">
        <div class="pop_item" @click="addImg('local')">相册上传</div>
        <div class="pop_item" @click="addImg('sj1')">随机1条</div>
      </div>
    </mt-popup>
    <!--选择时间日期的组件 -->
    <mt-popup v-model="setTimePop" class="app_pop_wapper">
      <chose-time :initTime="formInline.sendTime" @upDateTime="(data)=>{formInline.sendTime=data,setTimePop=false}" @close="setTimePop=false"></chose-time>
    </mt-popup>
    <!-- 假装的图片，为了得到朋友圈一张时的宽高 -->
    <div class="imnnn">
      <img ref="imgsss" width="auto" :src="dialogImageUrl">
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui';
import commBottom from '@/components/commonBottom'
import commonUpload from '@/components/commonUpload2'
import choseTime from '@/components/choseTime'
import "@/styles/makeInfo.scss"
import { returnImg } from "@/utils/makeJson.js"
var _this

import {
  GetDateAndHourStr,
  setLocal,
  getLocal,
  makeRandomCount,
  GetDateAndHourMake
} from '@/utils'
export default {
  // name: 'List',
  components: {
    commBottom,
    commonUpload,
    choseTime
  },
  props: {
    seePjIf: {
      // 是否拼接
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      setTimePop: false,
      ifPj: this.seePjIf,
      popupVisible8: false,
      dialogImageUrl: '',
      setUploadM: false,
      headImg: '',
      whiosGetImg: '', //控制哪部分内容要使用上传头像
      showDetail: false, //展示详情页
      badInp: '', //没用的表单信息
      makeSelfArrPl: [''], //循环生成自定义评论
      startDate: new Date(),
      formInline: { //表单信息
        hasZtl: false, //有无返回栏
        isSystemAz: true, //操作系统，安卓/苹果
        nickName: '', //昵称
        makeSelfPl: '', //存储自定义评论
        yourHeaderImg: '', //头像
        contentStyle: '内容', //朋友圈形式(内容、链接)
        realContent: '', //朋友圈内容
        localtion: '', //地点
        plType: '重复', //评论的类型(重复、自定义)
        realImgs: [], //朋友圈的图片们
        realImgsWid: '', //朋友圈的图片的宽
        realImgsHei: '', //朋友圈的图片的高
        smallImg: '', // 链接缩略图
        smallImgLink: 'http://www.baidu.com', // 链接网址
        smallContent: '', // 链接内容
        sendTime: GetDateAndHourStr(), // 发送时间
        sendTimeNoformat: new Date().getTime(), // 发送时间(未格式化)
        makeLikeCount: '', //点赞个数
        makeContentCount: '', //评论个数
        makeContent: '', //评论内容
      },
    }
  },
  mounted() {

    _this = this //赋值全局的this
    let getRoute = this.$route
    // 解决键盘弹起后将内容遮挡问题
    this.$nextTick(() => {
      (function(window, document) {
        var stop = document.body.scrollTop; //获取软键盘唤起前浏览器滚动部分的高度
        $('input[type="text"],textarea').each(function() {
          $(this).focus(function(e) {
            setTimeout(function() {
              document.activeElement.scrollIntoViewIfNeeded()
            }, 400)
          })
        })
      })(window, document)
    })
  },
  methods: {
    addFgDeatail() {
      // 增加地区分割
      this.formInline.localtion += ' · '
      this.$nextTick(() => {
        $('.diy_inp').find('input').focus()
      })
    },
    getSetTime() {
      // 选择时间组件显示
      this.setTimePop = true
    },
    backBtn() {
      // 返回
      this.$emit('close')
    },
    backUrlBtn() {
      // 回到首页重新选择朋友圈方式
      window.history.go(-1)
    },
    addImg(type) {
      // 增加点赞
      switch (type) {
        case 'local':
          // 本地上传
          this.controlChange('headImg', false)
          break;
        case 'sj1':
          // 随机1张
          this.whiosGetImg = 'headImg' //告诉应该把图片给谁
          this.setImg(returnImg(1, 0, 300))
          break;
      }
      // 渲染完，弹窗关闭
      this.$nextTick(() => {
        this.popupVisible8 = false
      })
    },
    delImg(item, index) {
      // 删除图片
      MessageBox.confirm('确认删除图片吗?').then(action => {
        console.log(item)
        this.formInline.realImgs.splice(index, 1)
      }, cancel => {
        console.log(index)
      });
    },
    setImg(urls) {
      // 从组件那里获取到头像
      console.log(urls)
      console.log(this.whiosGetImg)
      switch (this.whiosGetImg) {
        case 'headImg':
          this.formInline.yourHeaderImg = urls[0]
          // 头像图片显示
          $("#tx_div_wapper_mi").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;
        case 'linkImg':
          this.formInline.smallImg = urls[0]
          // 头像图片显示
          $("#link_div_wapper_p").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;
        case 'contentImg':
          // 朋友圈主图片
          this.formInline.realImgs = urls.concat(this.formInline.realImgs)
          // 如果只有一张图片，得到宽高
          if (this.formInline.realImgs && this.formInline.realImgs.length === 1) {
            this.dialogImageUrl = this.formInline.realImgs[0]
          }
          let t = setTimeout(() => {
            let dom = this.$refs.imgsss
            this.formInline.realImgsWid = dom.width
            this.formInline.realImgsHei = dom.height
            console.log(this.formInline.realImgsWid)
          }, 300)
          break;

      }
    },
    changeContentImg() {
      // 朋友圈图片
      this.controlChange('contentImg', true)
    },
    changeHeadImg() {
      // 头像图片
      this.popupVisible8 = true
    },
    changeLinkImg() {
      this.controlChange('linkImg', false)
    },
    controlChange(whios, ifM) {
      this.whiosGetImg = whios
      // 上传设置单选
      this.setUploadM = ifM
      // 修改头像,触发组件
      setTimeout(() => {
        this.$refs.loadImg.actions()
      }, 100)
    },
    imgInfo(...data) {
      console.log(data)
      let giveWho = data[0]
      let giveUrl = data[1].url
      this.formInline[giveWho] = giveUrl
    },
    showIndex() {
      // 显示首页内容
      this.showDetail = false
    },

    clearRealImgs() { //清空朋友圈内容图片
      this.formInline.realImgs = []
    },
    handleConfirm(data) { //时间的确定事件
      this.formInline.sendTimeNoformat = data //未格式化的时间
      let date = GetDateAndHourMake(data)
      this.formInline.sendTime = date
    },
    uploadBtnOne(refs) { //上传图片按钮
      // refs动态传来的input的ref
      // console.log(this.$refs.uploadRef)
      // this.$refs.uploadRef.getStart()
      // this.$refs[refs].click()
    },
    chenckTime() { //选择时间
      this.$refs.picker.open()
    },
    addNewPl() { //新增评论
      this.makeSelfArrPl.push('')
    },
    delNowContent(index) { //删除当前评论
      this.makeSelfArrPl.splice(index, 1)
    },
    contentChnangeStyle() { //朋友圈内容形式的变化
    },
    postForm() { //表单提交
      console.log(JSON.parse(JSON.stringify(this.formInline)))
      this.$emit('close')
      this.$emit('backInfoData', this.formInline)
    },
    delForm() { //清空表单和localStorage
      let _that = this
      let formInlineKeys = Object.keys(this.formInline)
      formInlineKeys.forEach((item, index) => {
        // 选择的内容类型不清空,其余清空
        if (item !== 'contentStyle') {
          if (typeof _that.formInline[item] === 'array') {
            _that.formInline[item] = []
          } else if (typeof _that.formInline[item] === 'string') {
            _that.formInline[item] = ''
          }
        }
      })
      this.clearRealImgs() //上传的图片清空
      localStorage.setItem('makeJson', '')

    },
    rebackData() { //从localStorage回显已经保存的数据
      let ccc = getLocal('makeJson')
      if (ccc !== null && ccc !== '' && ccc !== undefined) {
        this.formInline = ccc
      }
    }
  },
  watch: {
    seePjIf() {
      this.ifPj = this.seePjIf
    }

  }
};

</script>
<style scoped lang="scss">
$grey:#ecf3fd; //灰色
$blueFont:#616e91; //蓝色字体
$greyFont:#8a8a8a; //  灰色字体
$backArea:#eeeeee; //点赞去背景色
$greenFont:#69a44a; //绿色字体
$returnMesBk:#f5f5f5; //下方回复区域背景色 灰色
$returnMesBorder:#dcdcdc; //下方回复区域上边框颜色
$fontSizeCommon:40px;
$fontSizeSmall:35px;
$fontGold:#F5A623; //金色

.makeInfo_wapper {
  padding: 20px;
  overflow: auto;
  position: fixed;
  padding-bottom: 50px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-image: url('../assets/maldives_wedding-wallpaper-1280x800.jpg');

  .tx_wapper {
    height: 188px;
  }

  .newAdd_pl_wapper {
    text-align: center;
    margin-top: 30px;
  }

  .some_tips {
    text-align: center;
    font-size: $fontSizeSmall;
    background-color: #fff;
    padding: 20px;
    font-weight: bold;
    border-radius: 6px;
    margin-top: 10px;

    a {
      color: $fontGold;
    }
  }

  .inner_make_btn {
    margin-top: 30px;
    text-align: center;
  }

  .for_example_inner {
    color: red;
    margin: 20px 0;

    .inner_show_example {
      width: 100%;
      height: 400px;
      background-image: url('../assets/neirong.jpg');
      background-size: contain;
    }

    .pl_show_example {
      width: 100%;
      height: 400px;
      background-image: url('../assets/pyq.jpg');
      background-size: contain;
    }

    .inner_show_example_other {
      width: 100%;
      height: 300px;
      background-image: url('../assets/lianjie.jpg');
      background-size: contain;
    }

    .inner_show_example_other_all {
      width: 100%;
      height: 300px;
      background-image: url('../assets/ljwz.jpg');
      background-size: contain;
    }
  }

  .inner_index_content {
    opacity: .9;

    .make_fh_wapper {}

    .card_inner_text {
      text-align: center;
    }



    .inner_img_header {
      // flex: 0 0 102px;
      vertical-align: bottom;
      margin-right: 20px;
      width: 102px;
      height: 102px;
      display: inline-block;
    }
  }

  .link_div_wapper {
    width: 128px;
    height: 128px;
    background-image: url('../assets/fadelink.jpeg')
  }



  .nr_div_wapper {
    width: 128px;
    height: 128px;
    background-image: url('../assets/addImg.jpeg')
  }

  .tx_div_wapper {
    width: 128px;
    height: 128px;

    &.hasBg {
      background-image: url('../assets/nodoby.jpg')
    }
  }

  .app_wapper_box {
    padding: 20px 0;
    flex-wrap: wrap;

    .show_imgs {
      width: 128px;
      height: 128px;
      margin: 0 15px 15px 0;
    }
  }

  .imnnn {
    width: 1px;
    height: 1px;
    overflow: hidden;

    img {
      width: auto !important;
    }
  }

  .add_tbns {
    margin-left: 15px;
  }

  .set_pp_p {
    padding-right: 15px;
    margin-right: 15px;
    border-right: 1px solid #c5bdbd;
  }
}

.noShow {
  // display: none;
}

</style>



